<template>
  <div>
    <van-skeleton
      :row="7"
      :loading="
        productDetail && recom && recomBtm && contentList ? false : true
      "
      :row-width="rowHeight"
    >
      <div
        style="background-color: #f5f5f5"
        v-if="productDetail"
      >
        <!-- 头部 -->
        <div>
          <van-sticky>
            <header>
              <div class="icon" @click="$router.back()">
                <img src="@/assets/arrow_left_circle_gray2.png" alt="" />
              </div>
              <div class="headerBar"></div>
              <div class="icon">
                <img src="@/assets/share_circle_gray.png" alt="" />
              </div>
            </header>
          </van-sticky>
        </div>
        <!-- 轮播图 -->
        <ProductSwiper />

        <!-- 产品介绍 -->
        <div class="public productInfo">
          <div>
            <p class="price">
              <span>￥</span> {{ productDetail.goods_info[0].market_price }}
            </p>
            <div class="tip">
              <div class="discount">预计得229米金</div>
              <span
                >更多优惠 <img src="@/assets/discount_more.png" alt=""
              /></span>
            </div>
          </div>
          <p class="name">{{ productDetail.product_info?.name }}</p>
          <ol>
            <li
              v-for="(desc, idnex) in productDetail.product_info
                .sell_point_desc"
              :key="idnex"
            >
              {{ desc }}
            </li>
          </ol>
        </div>
        <!-- 参数 -->
        <!-- goods_info[0].class_parameters -->
        <div
          class="public"
          v-if="productDetail?.goods_info[0]?.class_parameters?.list"
        >
          <ParametersView />
        </div>
        <!-- 活动 -->
        <div class="local public">
          <p class="tag">活动</p>
          <div style="width: 80%; display: flex">
            <span
              style="
                color: rgb(255, 89, 0);
                font-size: 10px;
                border: 1px solid rgb(255, 89, 0);
                border-radius: 2px;
              "
              >超值换购</span
            ><span
              style="
                width: 224px;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                padding-left: 4px;
              "
              >加99.0元得建议零售价149元小米冰封散热背夹
              白色;加99.0元得建议零售价149元小米冰封散热背夹
              黑色;加79.0元得建议零售价149元Xiaomi 14 菱格素皮保护壳
              雪粉色;加189.0元得建议零售价199元小米 90W GaN 三口充电器套装
              (1A2C) 白色</span
            >
          </div>
          <p><van-icon name="arrow" size="10" @click="showServe = true" /></p>
        </div>
        <van-popup
          v-model="showActive"
          round
          position="bottom"
          :style="{ height: '60%' }"
        >
          <h1>活动</h1>
          <div>
            <div
              class="serve"
              v-for="(item, index) in productDetail.goods_info[0]
                .service_refound_policy_list.list_new"
              :key="index"
            >
              <div>
                <span
                  style="
                    color: rgb(255, 89, 0);
                    font-size: 10px;
                    border: 1px solid rgb(255, 89, 0);
                    border-radius: 2px;
                  "
                  >超值换购</span
                >
              </div>
              <div style="padding: 0 16px">
                <div style="color: rgba(0, 0, 0, 0.9); font-size: 14px">
                  低价换好物
                </div>
              </div>
            </div>
          </div>
        </van-popup>
        <!-- 选项 -->
        <div class="public">
          <!-- 规格 -->
          <specificationsViewVue />
          <!-- 地址 -->
          <div class="local" @click="showLocal = true">
            <p class="tag">地址</p>
            <div style="width: 80%">
              <van-icon name="location-o" />
              <span v-if="cityList" style="margin-left: 8px">
                <span v-for="(city, index) in cityList" :key="index"
                  >{{ city }}
                </span>
              </span>
              <span v-else>北京市 东城区 安定门街道</span>
              <div class="tip">
                <span>有现货</span><span>今天23时前送达</span>
              </div>
            </div>
            <p><van-icon name="arrow" size="10" /></p>
          </div>
          <van-popup
            v-model="showLocal"
            round
            position="bottom"
            :style="{ height: '60%' }"
          >
            <div v-show="showLocal">
              <van-area
                title="标题"
                :area-list="areaList"
                @confirm="confirm"
                @cancel="cancel"
              />
            </div>
          </van-popup>
          <!-- 服务 -->
          <div class="local">
            <p class="tag">服务</p>
            <div style="width: 80%">
              <span>小米自营 到店自取</span>
            </div>
            <p><van-icon name="arrow" size="10" @click="showServe = true" /></p>
          </div>
          <van-popup
            v-model="showServe"
            round
            position="bottom"
            :style="{ height: '60%' }"
          >
            <h1>服务说明</h1>
            <div>
              <div
                class="serve"
                v-for="(item, index) in productDetail.goods_info[0]
                  .service_refound_policy_list.list_new"
                :key="index"
              >
                <div>
                  <van-radio checked-color="#ff5900"></van-radio>
                </div>
                <div style="padding: 0 16px">
                  <span>{{ item.title }}</span>
                  <br />
                  <span class="desc">{{ item.desc }}</span>
                </div>
              </div>
            </div>
          </van-popup>
        </div>

        <!-- 评论 -->
        <div class="public">
          <div
            @click="
              $router.push({
                path: '/miquanproduct',
                query: {
                  product_id: $route.query.product_id,
                },
              })
            "
          >
            <div class="buyer">
              <span>买家秀</span>
              <span class="connent"
                >共334w+条评论 <van-icon name="arrow" size="10"
              /></span>
            </div>
            <div class="buyImg">
              <img
                v-for="(img, index) in contentList"
                :key="index"
                :src="img"
                alt=""
              />
            </div>
          </div>
        </div>

        <!-- 详情页前商品推荐 -->
        <div class="public">
          <h5>为你推荐</h5>
          <CommodityView :recom="recom" />
        </div>

        <template v-for="item in productDetail.goods_tpl_datas">
          <template v-for="(session, i) in item.sections">
            <div
              class="public"
              :key="i"
              v-if="session.view_type === 'product_info_wheel_ad'"
            >
              <img
                style="width: 100%"
                :src="session.body.items[0].img_url"
                alt=""
              />
            </div>
          </template>
        </template>

        <!-- 产品详情 -->
        <van-tabs v-model="active" animated>
          <van-tab
            v-for="(tab, index) in ['商品介绍', '规格参数']"
            :key="index"
            :title="tab"
          >
          </van-tab>
        </van-tabs>
        <template v-for="items in productDetail?.goods_tpl_datas">
          <template v-for="item in items.sections">
            <productInfoTab
              :key="item.book_id"
              :item="item"
              v-if="item.view_type === 'product_info_tab' && active === 0"
            />
            <productInfoParameter
              :key="item.book_id"
              :item="item"
              v-if="item.view_type === 'product_info_tab' && active === 1"
            />
          </template>
        </template>

        <!-- 底部产品推荐 -->
        <div style="margin-bottom: 50px">
          <h5 style="padding: 12px 0 0 12px; margin-bottom: 0">精选好物</h5>
          <CommodityBtmView :recomBtm="recomBtm" />
        </div>
      </div>
    </van-skeleton>
  </div>
</template>

<script>
import CommodityView from "@/components/ProductDetail/CommodityView.vue";
import CommodityBtmView from "@/components/ProductDetail/CommodityBtmView.vue";
import ProductSwiper from "@/components/ProductDetail/ProductSwiper.vue";
import specificationsViewVue from "@/components/ProductDetail/specificationsView.vue";
import ParametersView from "@/components/ProductDetail/ParametersView.vue";
import productInfoTab from "@/components/ProductDetail/productInfoTab.vue";
import productInfoParameter from "@/components/ProductDetail/productInfoParameter.vue";

import { mapActions, mapMutations, mapState } from "vuex";
import { Toast } from "vant";
import { areaList } from "@vant/area-data";
export default {
  name: "ProductDetails",
  components: {
    ProductSwiper,
    CommodityView,
    CommodityBtmView,
    specificationsViewVue,
    ParametersView,
    productInfoTab,
    productInfoParameter,
  },
  data() {
    return {
      recom: null,
      recomBtm: null,
      active: 0,
      // 地址
      areaList,
      showLocal: false,
      cityList: null,
      // 服务
      showServe: false,
      // 活动
      showActive: false,
      // 买家秀
      contentList: [],
      // gujia
      rowHeight: ["100vw", "30vw", "100vw"],
    };
  },
  computed: { ...mapState(["productDetail"]) },
  beforeRouteUpdate(to, from, next) {
    // console.log("beforeRouteEnter", to , from);
    if (to !== from) {
      this.getProductDetail(this.$route.query.product_id);
      this.getRecom();
      this.getRecomBtm();
      next();
    }
  },
  methods: {
    ...mapMutations(["delProductDetail"]),
    ...mapActions(["getProductDetail"]),
    // 详情前推荐
    getRecom() {
      this.axiosSearch
        .get("miproduct/recom?commodity_id=" + this.$route.query.product_id)
        .then((res) => {
          // console.log("getRecom", res.data.data.related_recommend);
          this.recom = res.data.data.related_recommend;
        });
    },
    // 详情后推荐
    getRecomBtm() {
      this.axiosSearch
        .get("miproduct/recom_btm?commodity_id=" + this.$route.query.product_id)
        .then((res) => {
          // console.log("getRecomBtm", res.data.data);
          this.recomBtm = res.data.data.related_recommend;
        });
    },
    onClickIcon() {
      Toast("请前往app");
    },
    // 地址
    confirm(selectedValues) {
      // console.log(selectedValues);
      this.cityList = selectedValues.map((item) => item.name);
      this.showLocal = false;
    },
    cancel() {
      this.showLocal = false;
    },
    // 买家秀
    getContentList() {
      this.axiosSearch
        .get("communicate/mizone_buyer_show_list", {
          params: {
            product_id: this.$route.query.product_id,
            size: 10,
            tag_id: 58527,
            tag_type: 2,
            page: 1,
          },
        })
        .then((res) => {
          // console.log("getContentList", res.data.data.comments[0].image_infos);
          let imgList = res.data.data.comments
            .filter(
              (item) => item.image_infos && Array.isArray(item.image_infos)
            )
            .map((item) => item.image_infos.map((img) => img.img_url));
          imgList.forEach(
            (item) => (this.contentList = [...this.contentList, ...item])
          );
          this.contentList = this.contentList.slice(0, 4);
          // console.log("getContentList", this.contentList);
        });
    },
  },
  created() {
    this.getProductDetail(this.$route.query.product_id);
    this.getRecom();
    this.getRecomBtm();
    this.getContentList();
  },
  beforeDestroy() {
    this.delProductDetail();
  },
};
</script>

