  width: 100vw;
  display: flex;
  justify-content: space-around;
  background: transparent;
  // backdrop-filter: blur(40px);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 99;
  padding: 12px 0;
  .icon {
    height: 27px;
    width: 27px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .headerBar {
    width: 70vw;
  }
}
.public {
  width: 90vw;
  margin: 8px auto;
  text-align: left;
  background: #fff;
  border-radius: 0.1rem;
  padding: 12px;
}
