  display: flex;
  justify-content: space-between;
  .connent {
    color: rgb(0, 0, 0);
    font-size: 11px;
    opacity: 0.5;
  }
}
.buyImg {
  padding-top: 0.2rem;
  img {
    border-radius: 4px;
    height: 80px;
    margin-right: 4px;
    width: 80px;
  }
}

.van-skeleton {
  padding: 0;
  margin-bottom: 12px;
}
.van-skeleton__row:nth-child(1){
  height: 427px;
}
.van-skeleton__row:nth-child(2){
  height: 37px;
}
.van-skeleton__row:nth-last-child(1){
  height: 60px;
}
</style>