<template>
  <div>
    <div class="typeName">{{ type.name }}</div>
    <ul>
      <li
        v-for="(item, i) in type.list"
        :key="item.prop_value_id"
        :class="{ active: typeIndex === i }"
        @click="updateTypeIndex(i, type.prop_cfg_id, item.prop_value_id)"
      >
        {{ item.name }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["type", "typeIndex"],
  methods: {
    updateTypeIndex(index, prop_cfg_id, prop_value_id) {
      this.$emit("updateTypeIndex", index, prop_cfg_id, prop_value_id);
    },
  },
};
</script>

<style lang="less" scoped>
.typeName {
  color: rgb(0, 0, 0);
  font-size: 12px;
  font-weight: 500;
}
ul {
  display: flex;
  //   justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 7.5px;
  margin-top: 10.5px;
  li {
    background-color: rgba(0, 0, 0, 0.04);
    border-color: rgb(255, 255, 255);
    border-radius: 13.5px;
    border-width: 1px;
    margin-bottom: 3px;
    margin-right: 11.5px;
    padding: 8px 13.5px;
    position: relative;
    font-size: 12px;
    &.active {
      background-color: rgba(255, 89, 0, 0.08);
      border-color: rgb(255, 89, 0);
      border-radius: 13.5px;
      border-width: 1px;
      margin-bottom: 3px;
      margin-right: 11.5px;
      padding: 5px 13.5px;
      position: relative;
      color: rgb(255, 89, 0);
    }
  }
}
</style>