<template>
  <van-swipe v-if="productDetail?.goods_share_datas?.gallery_view" @change="onChange" :autoplay="3000">
    <van-swipe-item
      v-for="(image, index) in productDetail?.goods_share_datas?.gallery_view"
      :key="index"
      ><img :src="image.img_url"
    /></van-swipe-item>
    <template #indicator>
      <div class="custom-indicator">
        {{ current + 1 }}/{{
          productDetail.goods_share_datas.gallery_view.length
        }}
      </div>
    </template>
  </van-swipe>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      current: 0,
    };
  },
  computed: { ...mapState(["productDetail"]) },
  methods: {
    onChange(index) {
      this.current = index;
    },
  },
};
</script>

<style lang="less" scoped>
.van-swipe-item {
  width: 375px;
  height: 412.5px;
  background-color: transparent;
  float: left;
  position: relative;
  left: 0px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.custom-indicator {
  position: absolute;
  padding: 2px 5px;
  font-size: 12px;
  background-color: rgba(51, 51, 51, 0.3);
  border-radius: 176px;
  bottom: 14.5px;
  height: 21px;
  min-width: 38.5px;
  right: 10.5px;
  line-height: 21px;
  color: #fff;
  text-align: center;
}
</style>