<template>
  <div class="specifications" @click.self="showPopup" v-if="productDetail?.goods_info">
    <p class="tag">已选</p>
    <div style="width: 70vw" @click="showPopup">
      <div class="userProduct">
        {{ userProduct[0]?.name }}
      </div>
      <div class="service">可选增值服务</div>
      <div class="mishop">
        <img class="image" v-for="(img, index) in ProductImage.slice(0, 4)" :key="index" :src="img" alt="" />
        <div class="tips">
          <div>共{{ productDetail?.buy_option[1]?.list?.length }}种</div>
          <div>颜色可选</div>
        </div>
      </div>
    </div>
    <p><van-icon name="arrow" size="10" @click="showPopup" /></p>

    <!-- 弹出层 -->
    <!-- goods_info[1].prop_list -->
    <van-popup v-model="show" round position="bottom" closeable :style="{ height: '70%' }">
      <div style="padding: 16px">
        <van-sticky :offset-top="210">
          <div class="proInfo" v-if="userProduct.length > 0">
            <img :src="userProduct[0]?.img_url" alt="" @click="showImage = true" />
            <!-- 图片预览 -->
            <div>
              <p class="price">￥{{ userProduct[0]?.price }}</p>
              <h5 class="name">
                {{ userProduct[0]?.name }}
              </h5>
            </div>
          </div>
          <div class="proInfo" v-else>
            <img :src="ProductImage[0]" alt="" />
            <!-- 图片预览 -->
            <div>
              <h5 style="font-size: 0.3rem">暂时缺货</h5>
            </div>
          </div>
        </van-sticky>

        <div class="local"></div>
        <TypeView class="type" :type="productDetail.buy_option[0]" :typeIndex="typeIndex"
          @updateTypeIndex="updateTypeIndex" />
        <TypeView class="type" :type="productDetail.buy_option[1]" :typeIndex="colorIndex"
          @updateTypeIndex="updateTypeIndex" />
        <div class="count">
          <span>购买数量</span>
          <van-stepper v-model="value" />
        </div>
        <div class="btn">
          <van-goods-action-button type="warning" text="加入购物车" @click="addToCar" />
          <van-goods-action-button type="danger" @click="addOrder" text="立即购买" />
        </div>
      </div>
    </van-popup>

    <!-- 图片预览 -->
    <van-popup v-model="showImage" :overlay-style="{ 'background-color': '#000' }">
      <van-swipe class="my-swipe" indicator-color="white" style="height: 55vh;width: 100vw;">
        <van-swipe-item v-for="(img, index) in ProductImage" :key="index" style="width: 100vw;height: 100%;">
          <img :src="img" alt="" style="width: 100%;height: 100%;">
        </van-swipe-item>
      </van-swipe>
    </van-popup>

    <!-- 加入购物车 -->
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" />
      <van-goods-action-icon icon="cart-o" text="购物车" @click="$router.push('car')" />
      <van-goods-action-icon icon="shop-o" text="首页" @click="$router.push('/')" />
      <van-goods-action-button type="warning" @click="showPopup" text="加入购物车" />
      <van-goods-action-button type="danger" @click="showPopup" text="立即购买" />
    </van-goods-action>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import TypeView from "./TypeView.vue";
import { Toast } from "vant";

export default {
  components: {
    TypeView,
  },
  data() {
    return {
      show: false,
      // 预览图片
      showImage: false,
      // 已选规格
      //   userProduct: "12GB+256GB 墨羽",
      // 购买数量
      value: 1,
      // 默认规格
      typeIndex: 0,
      colorIndex: 0,
      type_value_id: 0,
      type_cfg_id: 0,
      color_value_id: 0,
      color_cfg_id: 0,
      // 类型
      goods: null,
    };
  },
  computed: {
    ...mapState(["productDetail", "token", "carList", "orderList"]),
    // 选择的手机类型
    userProduct() {
      //   return this.goods_prop_list
      return this.goods?.filter((goods) => {
        return (
          goods.prop_list[0]?.prop_cfg_id === this.type_cfg_id &&
          goods.prop_list[0]?.prop_value_id === this.type_value_id &&
          goods.prop_list[1]?.prop_cfg_id === this.color_cfg_id &&
          goods.prop_list[1]?.prop_value_id === this.color_value_id
        );
      });
    },
    ProductImage() {
      return this.goods?.map((goods) => goods.img_url);
    },
  },
  watch: {
    productDetail(newVal) {
      if (newVal) {
        this.initializeProduct();
      }
    },
  },
  methods: {
    ...mapMutations(["updateCar", "updateOrder"]),
    showPopup() {
      this.show = true;
    },
    // 修改typeIndex
    updateTypeIndex(index, prop_cfg_id, prop_value_id) {
      if (prop_cfg_id == "8") {
        this.typeIndex = index;
        this.type_value_id = prop_value_id;
        this.type_cfg_id = prop_cfg_id;
      } else {
        this.colorIndex = index;
        this.color_value_id = prop_value_id;
        this.color_cfg_id = prop_cfg_id;
      }
    },

    //   初始化产品分类
    initializeProduct() {
      this.goods = this.productDetail.goods_info;
      this.type_value_id =
        this.productDetail.buy_option[0]?.list[0]?.prop_value_id;
      this.type_cfg_id = this.productDetail.buy_option[0]?.prop_cfg_id;
      this.color_value_id =
        this.productDetail.buy_option[1]?.list[0]?.prop_value_id;
      this.color_cfg_id = this.productDetail.buy_option[1]?.prop_cfg_id;
    },

    // 添加购物车
    addToCar() {
      if (this.userProduct.length === 0) {
        Toast('暂时缺货')
        return
      }
      if (this.token) {
        let hasProduct = this.carList.find((item) => {
          return (
            item.token === this.token &&
            item[0].name === this.userProduct[0].name
          );
        });
        // 有重复数量加一
        if (hasProduct) {
          let addCarList = this.carList.map((item) => {
            if (
              item.token === this.token &&
              item[0].name === this.userProduct[0].name
            ) {
              return { ...item, count: item.count + this.value };
            }
            return item;
          });
          localStorage.setItem("carList", JSON.stringify(addCarList));
          console.log(addCarList);
        } else {
          let addCarList = [
            ...this.carList,
            {
              ...this.userProduct,
              token: this.token,
              count: this.value,
              ischecked: false,
            },
          ];
          localStorage.setItem("carList", JSON.stringify(addCarList));
          console.log(addCarList);
        }
        Toast("已添加进购物车");
        this.value = 1;
        this.updateCar();
      } else {
        Toast("请先登录");
        this.$router.replace({
          path: '/login',
          query: {
            target: this.$route.fullPath
          }
        })
      }
    },
    // 购买订单
    addOrder() {
      let order_id = Date.now() + this.$route.query.product_id;
      // 判断是否登录
      if (this.token) {
        let addOrderList = [
          ...this.orderList,
          {
            ...this.userProduct,
            token: this.token,
            count: this.value,
            state: "未支付",
            id: order_id,
          },
        ];
        localStorage.setItem("orderList", JSON.stringify(addOrderList));
        console.log(addOrderList);
        this.$router.push({
          path: "/buypage",
          query: {
            order_id: order_id,
          },
        });
        // Toast("已添加进购物车");
        // this.value = 1;
        this.updateOrder();
      } else {
        Toast("请先登录");
        this.$router.replace({
          path: '/login',
          query: {
            target: this.$route.fullPath
          }
        })
      }
    },
    onClickIcon() { },
  },
  created() {
    if (this.productDetail) {
      this.initializeProduct();
      // console.log(this.goods);
    }
  },
};
</script>

<style lang="less" scoped>
// 规格
.specifications {
  display: flex;
  justify-content: space-between;
  position: relative;

  .tag {
    color: rgb(0, 0, 0);
    font-size: 12px;
    opacity: 0.5;
    width: 37.5px;
  }

  .userProduct {
    color: rgb(0, 0, 0);
    font-size: 12px;
    opacity: 0.9;
  }

  .service {
    color: rgb(255, 89, 0);
    font-size: 12px;
    margin-top: 5px;
  }

  .mishop {
    display: flex;
    align-items: center;

    .image {
      background-color: rgba(0, 0, 0, 0.02);
      border-radius: 4px;
      height: 41.5px;
      width: 41.5px;
      margin-right: 4px;
    }

    .tips {
      background-color: rgb(248, 248, 248);
      border-radius: 4px;
      height: 41.5px;
      margin-left: 6.5px;
      width: 62.5px;

      div {
        color: rgb(0, 0, 0);
        font-size: 11px;
        line-height: 17px;
        opacity: 0.5;
        text-align: center;
      }
    }
  }

  .proInfo {
    display: flex;

    img {
      background-color: rgba(0, 0, 0, 0.02);
      border-radius: 6.5px;
      right: 16.5px;
      background-color: rgb(242, 242, 242);
      border-radius: 6.5px;
      height: 86.5px;
      margin-right: 16.5px;
      width: 86.5px;
    }

    .price {
      color: rgb(255, 89, 0);
      font-size: 19px;
      font-weight: 500;
    }

    .name {
      color: rgb(0, 0, 0);
      font-size: 13px;
      font-weight: 500;
      line-height: 20px;
      margin-top: 8.5px;
    }
  }

  .local {}

  .type {
    margin-top: 12.5px;
  }

  .count {
    display: flex;
    justify-content: space-between;
    margin-top: 23px;
    margin-bottom: 44px;
    // padding-left: 15.5px;
    padding-right: 15.5px;
  }

  span {
    color: rgb(0, 0, 0);
    font-size: 12px;
    font-weight: 500;
  }

  .btn {
    display: flex;
    position: fixed;
    bottom: 0.1rem;
    left: 0;
    width: 100vw;
  }
}

.van-goods-action {
  z-index: 9;
}

.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #fff;
}

.van-overlay {
  background: #000;
}
</style>