<template>
  <div class="parameters">
    <div
      class="parameterItem"
      v-for="(parameter, index) in parametersList"
      :key="index"
    >
      <!-- <div v-if="parameter.icon"> -->
      <img :src="parameter.icon" alt="" />
      <div class="title">{{ parameter.top_title }}</div>
      <div class="value">{{ parameter.value }}</div>
      <!-- </div> -->
    </div>
  </div>
  <!-- goods_info[0].class_parameters -->
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      list: null,
    };
  },
  computed: {
    ...mapState(["productDetail"]),
    parametersList() {
      return this.list.filter((item) => item.is_page_show);
    },
  },
  created() {
    this.list = this.productDetail?.goods_info[0].class_parameters.list;
  },
};
</script>

<style lang="less" scoped>
.parameters {
  display: flex;
  overflow: auto;
  text-align: center;
  scrollbar-width: none; /* 隐藏滚动条 */
  .parameterItem {
    margin-right: 5px;
    width: 70px;
    flex: 1;
    padding: 8px 4px;
    img {
      height: 16.5px;
      width: 16.5px;
    }
    .title {
      color: rgba(0, 0, 0, 0.9);
      font-size: 12px;
      line-height: 20px;
      margin-top: 4px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .value {
      color: rgba(0, 0, 0, 0.5);
      font-size: 11px;
      line-height: 17px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>